.firstPage{
  height: calc(100vh - 70px);
  background-image: url(https://www.bhbarbershop.com/wp-content/uploads/2018/02/benefits-of-beard-oil.jpg);
  background-position: center top;
  background-size: cover;
  display:flex;
}
.navBar{
  height: 100px;
  width: calc(100vw - 70px);
  border: solid white 2px ;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 35px;
  display: flex;
  flex-direction: row;  
  align-items: center;
  padding: 17.5px;
}
.secondPage{
  width: 100vw;
  height: 200vh;
}


.scritteLaterali{
  height: 100%;
  display: flex;
  flex-direction: column;
}
.daFabrizio{
  margin-top: auto;
  color: white;
  margin-left: 70px;
  font-size: 35px;
}
.via{
  margin-bottom: auto;
  margin-top:0px;
  margin-left: 70px;
  color: white;
  font-family: 'Playfair Display', serif;
  font-weight: 400;
  font-size: 17.5px;
}
.material-symbols-outlined {
  font-variation-settings:
  'FILL' 0,
  'wght' 400,
  'GRAD' 0,
  'opsz' 48;
  color: white;
  margin-left: auto;
  font-size: 35px;
}
.material-symbols-outlined::before{
  content: '';
  height: 12px;
  width: 0px;
  border-left: 1px solid rgb(148, 148, 148);
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  margin-left: -17.5px;
}